header{
    height: 100vh;
    padding-top: 5rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}


/*================= CTA ================*/

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}


/*================= HEADER SOCIALS ================*/
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}


.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background-color: var(--color-primary);
}


/*================= ME ================*/
.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 15rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 7rem);
    margin-top: 3rem;
    border-radius: 9rem 9rem 0 0;
    overflow: hidden;
    padding: 3rem 1.5rem 1.5rem 1.5rem;
    
}



/*================= SCROLL DOWN ================*/

.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.8rem;
}

/* ============ MEDIA QUEERIES (MEDIUM DEVICE) ==========*/
@media screen and(max-width: 1024px){
    header{
        height: 68vh;
    }
}

/* ============ MEDIA QUEERIES (SMALL DEVICE) ==========*/
@media screen and(max-width: 600px){
    header{
        height: 100vh;
    }
    
    .header__socials,
    .scroll__down{
        display: none;
    }
}
